@import url(https://fonts.googleapis.com/css2?family=Heebo&display=swap);
body {
  margin: 0;
  font-family: "Heebo", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /*background: #3a1c71; /* fallback for old browsers */
  /*background: -webkit-linear-gradient(
    to right,
    #c59c81,
    #ca9a9f,
    #4d3e69
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f7dece,
    #d6aeb2,
    #beb0d8
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Form-details {
  display: flex;
  flex-direction: column;
}
.Name {
  margin-left: 10px;
  margin-bottom: 10px;
}
.Label-Quantity {
  margin-left: -30px;
}
.Quantity {
  margin-left: 10px;
  margin-bottom: 10px;
}
.Button {
  width: 80px;
  margin: auto;
  background-color: #c5c741;
  border: none;
  padding: 5px 7px;
  text-align: center;
  border-radius: 5px;
  position: relative;
  right: 18px;
}
.Button:hover {
  background: greenyellow;
}
input {
  font-size: 15px;
  color: blue;
  background-color: #f7f7f7;
  padding: 5px 5px 5px 5px;
  box-sizing: border-box;
  border-radius: 5px;
}
input:focus {
  background: #f7f7f7;
  border: 2px solid #c5c741;
  outline: none;
}

.Lists {
  margin-top: 50px;
  text-align: center;
  text-transform: uppercase;
}
ul {
  list-style-position: inside;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  border-radius: 10px;
}

li {
  width: 200px;
  margin: auto;
  padding-left: 40px;
  text-align: left;
  height: 40px;
  background: #ffff;
  line-height: 40px;
  color: #666;
  list-style: none;
}
li:nth-child(2n) {
  background: #f7f7f7;
}
button {
  margin-left: -10px;
  margin-right: 8px;
  cursor: pointer;

  border: none;
  transition: background 0.5s ease-out;
}
.fas {
  font-size: 1.1rem;
  color: #ff6666;
}
button .fas:hover {
  color: red;
}
.Button-clearAll {
  position: absolute;
  display: inline;
  margin-top: -25px;
  margin-left: 55px;
}
.Button {
  width: 80px;
  background-color: #c5c741;
  border: none;
  padding: 5px 7px;
  text-align: center;
  border-radius: 5px;
}
.Button:hover {
  background: greenyellow;
}
p {
  font-size: 0.9rem;
  color: #ff6666;
  font-weight: bold;
}

